<template>
  <div class="fh fh w">
<iframe class="video" :src="getlink()" frameborder="0"  height="48%" width="98%"></iframe>
<iframe class="video" :src="getlink()" frameborder="0"  height="48%" width="98%"></iframe>
  </div>
</template>

<script>
import {DataVideo} from '../data/Video'
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Modau',
  props: {},
  methods: {
    getlink(){
      return "https://www.youtube.com/embed/" +this.youtube_parser(DataVideo.getlistvideo());
    },
    youtube_parser(url) {
      var regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/;
      var match = url.match(regExp);
      return (match && match[7].length == 11) ? match[7] : "";

    },
  }
}
</script>

