var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bgs",style:({ backgroundImage: 'url(bg/bglon2.webp),url(bg/bglon1.webp)' })},[_c('b-row',{staticClass:"fh"},[_c('b-col',{attrs:{"cols":"2"}},[_c('LeftCom')],1),_c('b-col',{attrs:{"cols":"8"}},[_c('b-row',{staticClass:"bookview"},[_c('div',{staticClass:"book"},[(_vm.kkk > 0)?_c('div',{staticClass:"pages",attrs:{"id":"pages"}},[_c('div',{staticClass:"page",class:_vm.getCl(0),style:(_vm.getZindex(0)),on:{"click":function($event){return _vm.clicks(0)}}},[_c('div',{staticClass:"book",style:({ backgroundImage: 'url(bg/a.png), url(' + _vm.anh + ')' })})]),_c('div',{staticClass:"page2",class:_vm.getCl(1),style:(_vm.getZindex(1))},[_c('Modau')],1),_c('div',{staticClass:"page scolls",class:_vm.getCl(2),style:(_vm.getZindex(2)),on:{"click":function($event){return _vm.clicks(2)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Năm 1910, khi Burgess lần đầu giới thiệu quyển sách đầu tay “Những chuyện kể cùng Mẹ Già Gió Tây”, ông quả thực có chút lo lắng rằng biết đâu tác phẩm của mình sẽ không được mến mộ. Thế nhưng thực tế đã cho một câu trả lời hoàn toàn bất ngờ. Tính cho đến thời điểm Burgess qua đời vào năm 1965, những tác phẩm của ông được in đi in lại nhiều lần, cả dưới dạng sách bìa cứng lẫn các tập sách mỏng, bìa mềm cùng rất nhiều tranh minh họa. Ngay cả sau khi Burgess mất, đời sống của những tác phẩm mang đâm chất hơi thở đồng xanh này vẫn tiếp tục duy trì và tỏa hương sắc, vượt qua biên giới của Mỹ để đến với nhiều trẻ em khắp nơi trên thế giới. "),_c('img',{staticStyle:{"width":"555px","height":"370px"},attrs:{"alt":"Ra đời từ cách đây cả trăm năm, nhưng Mẹ già Gió Tây, Thỏ Peter, Già Ếch cùng thế giới loài vật độc đáo, cá tính trong những câu chuyện của Thornton Burgess cho đến nay vẫn chiếm trọn trái tim của bạn đọc nhỏ tuổi.","src":"https://cotich.net/uploads/images/bo-sach-nhung-chuyen-ke-cua-thornton-burgess-1.jpg"}}),_c('em',[_vm._v("Ra đời từ cách đây cả trăm năm, nhưng Mẹ già Gió Tây, Thỏ Peter, Già Ếch cùng thế giới loài vật độc đáo, cá tính trong những câu chuyện của Thornton Burgess cho đến nay vẫn chiếm trọn trái tim của bạn đọc nhỏ tuổi.")]),_vm._v(" Trên những trang web của các nhà xuất bản, người ta nhắc về những kỷ niệm với bộ sách "),_c('em',[_c('strong',[_vm._v("Mẹ Già Gió Tây")])]),_vm._v(" như thể đó những điều ngọt ngào của ấu thơ. Rất nhiều gia đình Mỹ suốt bốn thế hệ từ cụ kỵ đến cháu chắt đã chuyền tay nhau đọc những câu chuyện về Già Ếch, về Cáo Reddy, Thỏ Peter, Bầy gió con tinh nghịch… Chính vì thế mà bộ sách liên tục được các nhà xuất bản tái bản và có nhiều phiên bản minh họa khác nhau.  Xem thêm: "),_c('em',[_vm._v("Mẹ già Gió Tây, Thỏ Peter, Già Ếch - Những nhân vật tạo nên ký ức tuổi thơ tuyệt vời")])])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(3),style:(_vm.getZindex(3)),on:{"click":function($event){return _vm.clicks(3)}}},[_c('div',{staticClass:"box-content"},[_c('h3',[_vm._v(" Mẹ già Gió Tây và những câu chuyện bay bổng, phiêu lưu")]),_vm._v(" Mẹ Già Gió Tây bay xuống từ những đỉnh Đồi Tím trong ánh nắng ban mai óng vàng. Bà quàng trên vai một túi lớn, rất lớn, trong đó chứa tất cả những đứa con của bà - bầy Gió Con Nhí Nhảnh. Chúng sẽ rong chơi cả ngày nơi Đồng Xanh, Rừng Thẳm và nghe biết bao câu chuyện kể thú vị về chú Thỏ Peter tò mò, cậu Cáo Reddy ranh mãnh hay Già Ếch trầm ngâm thông thái. Nào cùng theo chân bầy Gió Con đến những miền truyện miên man đang chờ đón! "),_c('img',{staticStyle:{"margin":"0px","padding":"0px","border":"none","max-width":"100%","height":"370px","color":"rgb(17, 17, 17)","font-family":"Roboto","font-size":"16.5px","background-color":"rgb(252, 250, 246)","width":"555px"},attrs:{"alt":"Những câu chuyện cùng Mẹ già Gió Tây đã trở thành một trong những câu chuyện đọc trước giờ đi ngủ được trẻ em trên toàn thế giới yêu thích.","src":"https://cotich.net/uploads/images/me-gia-gio-tay-tho-peter-gia-ech-2.jpg"}}),_c('br')])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(4),style:(_vm.getZindex(4)),on:{"click":function($event){return _vm.clicks(4)}}},[_c('div',{staticClass:"box-content"},[_c('em',[_vm._v("Những câu chuyện cùng Mẹ già Gió Tây đã trở thành một trong những câu chuyện đọc trước giờ đi ngủ được trẻ em trên toàn thế giới yêu thích.")]),_vm._v(" Mặc dù xuất bản cách đây hơn 100 năm, những bài học trong cuốn sách dường như vẫn còn nguyên giá trị cũng như những cuộc phiêu lưu của các nhân vật vẫn không ngừng lôi cuốn các bạn đọc nhí. Những cậu chuyện nho nhỏ như thể những mẩu ngụ ngôn hiện đại dành cho trẻ em. Trong thế giới tươi đẹp muôn màu ấy, các bạn nhỏ vừa được nghe kể hăng say về nguồn gốc của các loài, vừa được cười nắc nẻ với những trò chơi khăm ranh mãnh, hay trầm trồ thán phục trước những tấm lòng hào hiệp, trượng nghĩa, sẵn lòng ra tay giúp đỡ lẫn nhau khi nguy khốn. Mỗi loài vật lại được khắc họa với những nét tính cách riêng độc đáo, khái quát, đã trở thành biểu tượng kinh điển và sẽ in sâu trong tâm trí của mỗi bạn nhỏ. "),_c('img',{staticStyle:{"width":"555px","height":"370px"},attrs:{"alt":"Mẹ già Gió Tây và những câu chuyện bay bổng, phiêu lưu","src":"https://cotich.net/uploads/images/me-gia-gio-tay-nhung-cau-chuyen-bay-bong-phieu-luu-1.jpg"}}),_vm._v(" Cuốn sách được chia thành những chương ngắn, mỗi ngày chỉ cần dành ra 20 phút để đọc hết một chương, thích hợp để trẻ đọc trước khi đi ngủ. Với những bạn lớn hơn và có thể tự đọc, thì những từ ngữ gợi hình cùng lối diễn đạt tự nhiên sẽ cuốn hút các em, đưa các em vào mạch đọc một cách dễ dàng, cùng trình bày hợp lý với những tranh minh họa tuyệt đẹp về thiên nhiên và muôn loài chắc chắn sẽ hạ gục không chỉ các em nhỏ mà ngay thậm chí cả người lớn, để tình yêu sách được bắt đầu đâm chồi nảy nở trong trẻ. ")])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(5),style:(_vm.getZindex(5)),on:{"click":function($event){return _vm.clicks(5)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" “Những câu chuyện cùng Mẹ già Gió Tây” là một cuốn sách đọc trước giờ đi ngủ đầy bay bổng và lôi cuốn thuộc bộ sách văn học thiếu nhi kinh điển “Những chuyện kể của Thornton Burgess”. Các bạn nhỏ sẽ nhận được những lợi ích khổng lồ khi đọc cuốn sách như: - Phát triển năng lực cảm xúc, xây dựng nền tảng đạo đức và vun đắp tình yêu thiên nhiên. - Bồi đắp vốn từ vựng khổng lồ, nuôi dưỡng năng lực diễn đạt ngôn ngữ và khả năng viết văn. - Gắn bó và chia sẻ cảm xúc với bố mẹ. - Cùng vô số kiến thức thú vị về thế giới loài vật vui tươi sống động... ")])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(6),style:(_vm.getZindex(6)),on:{"click":function($event){return _vm.clicks(6)}}},[_c('div',{staticClass:"box-content"},[_c('h3',[_vm._v(" Theo chân Già Ếch ngao du khám phá thế giới thiên nhiên tươi đẹp")]),_vm._v(" Già Ếch vốn mê Hồ Mỉm Chi đến độ không tài nào tin rằng có chỗ lại đáng yêu hơn nơi này. Ấy thế mà một ngày kia, già lại phải đứng trước lựa chọn: lưu lại quê hương thơ ấu hay chu du Thế Giới Bao La, cái nào mới hay ho nhất? Cùng xem già ấy đã phải đương đầu với những hiểm nguy sóng gió gì trong chuyến đi cực ly kỳ này nhé. "),_c('img',{staticStyle:{"margin":"0px","padding":"0px","border":"none","max-width":"100%","height":"370px","color":"rgb(17, 17, 17)","font-family":"Roboto","font-size":"16.5px","background-color":"rgb(252, 250, 246)","width":"555px"},attrs:{"alt":"Theo chân Già Ếch ngao du khám phá thế giới thiên nhiên tươi đẹp","src":"https://cotich.net/uploads/images/me-gia-gio-tay-tho-peter-gia-ech-4.jpg"}}),_vm._v(" Cuốn sách này hẳn sẽ gợi ra bao hứng khởi cho các bạn nhỏ, đưa các bạn theo chân Già Ếch vào một hành trình đầy hồi hộp và lôi cuốn. Già Ếch vốn trước nay rất nổi tiếng vì sự thông thái, am tường mọi nhẽ, ấy thế mà trong chuyến phiêu lưu này, Già Ếch đã rơi vào không biết bao nhiêu là phiền toái mà kể. Lối kể chuyện đầy lôi cuốn và dí dỏm của tác giả Thornton Burgess đã khiến những cuộc phiêu lưu của Già Ếch trở thành niềm ao ước mơ mộng của không biết bao nhiêu thế hệ bạn đọc nhỏ tuổi. ")])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(7),style:(_vm.getZindex(7)),on:{"click":function($event){return _vm.clicks(7)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Tuy nhiên, nhờ có sự giúp đỡ của các bạn thú tốt bụng và hào hiệp mà Già đã thoát khỏi những hiểm nguy đe dọa đến tính mạng mình. Các chương truyện ngắn giúp trẻ có thể đọc dễ dàng hơn nhưng lại không kém phần kịch tính và khiến con tò mò háo hức, muốn tiếp tục đọc nhiều hơn nữa. Đây sẽ là một lựa chọn vô cùng phù hợp để nuôi dưỡng và ươm mầm tình yêu đọc sách ở trẻ. "),_c('img',{staticStyle:{"width":"555px","height":"555px"},attrs:{"alt":"Theo chân Già Ếch ngao du khám phá thế giới thiên nhiên tươi đẹp","src":"https://cotich.net/uploads/images/gia-ech.jpg"}}),_vm._v(" Cuốn sách được chia thành những chương ngắn, mỗi ngày chỉ cần dành ra 20 phút để đọc hết một chương, thích hợp để trẻ đọc trước khi đi ngủ. Với những bạn lớn hơn và có thể tự đọc, thì những từ ngữ gợi hình cùng lối diễn đạt tự nhiên sẽ cuốn hút các em, đưa các em vào mạch đọc một cách dễ dàng, cùng trình bày hợp lý với những tranh minh họa tuyệt đẹp về thiên nhiên và muôn loài chắc chắn sẽ hạ gục không chỉ các em nhỏ mà ngay thậm chí cả người lớn, để tình yêu sách được bắt đầu đâm chồi nảy nở trong trẻ. "),_c('img',{staticStyle:{"width":"555px","height":"555px"},attrs:{"alt":"Theo chân Già Ếch ngao du khám phá thế giới thiên nhiên tươi đẹp","src":"https://cotich.net/uploads/images/gia-ech-2.jpg"}})])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(8),style:(_vm.getZindex(8)),on:{"click":function($event){return _vm.clicks(8)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" “Cuộc phiêu lưu của Già Ếch” là một cuốn sách đọc trước giờ đi ngủ đầy tình cảm và ly kỳ thuộc bộ sách văn học thiếu nhi kinh điển “Những chuyện kể của Thornton Burgess”. Bên cạnh đó, trẻ còn nhận được những lợi ích khổng lồ khi đọc cuốn sách như: - Phát triển năng lực cảm xúc, xây dựng nền tảng đạo đức và vun đắp tình yêu thiên nhiên. - Bồi đắp vốn từ vựng khổng lồ, nuôi dưỡng năng lực diễn đạt ngôn ngữ và khả năng viết văn. - Gắn bó và chia sẻ cảm xúc với bố mẹ. - Cùng vô số niềm vui trong chuyến phiêu lưu chân trời, góc bể cùng Già Ếch. ")])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(9),style:(_vm.getZindex(9)),on:{"click":function($event){return _vm.clicks(9)}}},[_c('div',{staticClass:"box-content"},[_c('h3',[_vm._v(" \"Thỏ Peter và những câu chuyện về sự tích muôn loài\" - Cuốn sách dành cho những đứa trẻ thích hỏi \"vì sao\"")]),_vm._v(" Già Ếch đã già lắm và biết rất nhiều chuyện, tất tần tật về thuở xa xưa khi thế giới tuổi còn xanh. Những khi cao hứng, già ấy rất thích kể về những ngày xửa ngày xưa đó. Mà Thỏ Peter vốn tò mò và có bao nhiêu là câu hỏi về muôn loài quanh mình. "),_c('img',{staticStyle:{"margin":"0px","padding":"0px","border":"none","max-width":"100%","height":"auto","color":"rgb(17, 17, 17)","font-family":"Roboto","font-size":"16.5px","background-color":"rgb(252, 250, 246)"},attrs:{"ch":"","i":"","n":"","ng":"","peter":"","src":"https://cotich.net/uploads/images/me-gia-gio-tay-tho-peter-gia-ech-3.jpg","u":""}}),_vm._v(" Vì sao Cáo Reddy có bộ áo đỏ, vì sao Chuột Xạ Jerry xây nhà dưới nước, vì sao ông Rắn không thể nháy mắt? ")])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(10),style:(_vm.getZindex(10)),on:{"click":function($event){return _vm.clicks(10)}}},[_c('div',{staticClass:"box-content"},[_vm._v(" Thế là Già Ếch bèn ngồi trên chiếc lá súng ở Hồ Mỉm Chi thân quen và kể cho Thỏ Peter cùng bè bạn nghe về những sự tích đầy bí ẩn đó. Các em bé thường có rất nhiều câu hỏi “Vì sao”. Ấy là bởi các em đang khám phá và tìm hiểu về thế giới xung quanh mình. Những mẩu truyện nhỏ vô cùng đặc biêt và dí dỏm trong tập sách này sẽ mang đến cho các em không chỉ những giờ phút đọc sách vui vẻ, mà hơn nữa là những kiến thức khoa học vô cùng xác đáng cũng như chắp cánh cho trí tưởng tượng và khả năng cảm thụ văn học. "),_c('img',{staticStyle:{"width":"555px","height":"555px"},attrs:{