úc, xây dựng nền tảng đạo đức và vun đắp tình yêu thiên nhiên. - Bồi đắp vốn từ vựng khổng lồ, nuôi dưỡng năng lực diễn đạt ngôn ngữ và khả năng viết văn. - Gắn bó và chia sẻ cảm xúc với bố mẹ. - Cùng vô số kiến thức thú vị về thế giới loài vật vui tươi sống động... ")])]),_c('div',{staticClass:"page scolls",class:_vm.getCl(13),style:(_vm.getZindex(13)),on:{"click":function($event){return _vm.clicks(13)}}},[_c('div',{staticClass:"box-content"},[_c('h3',[_vm._v(" Đời sống tái sinh của những phiên bản mới")]),_vm._v(" Trong những năm gần đây, chúng ta chứng kiến sự quay trở lại đầy ngoạn mục của các tác phẩm văn học kinh điển. Penguin, Harper Collins cùng nhiều đơn vị khác cho ra mắt những phiên bản được làm lại bìa kèm các tranh minh họa bắt mắt. Đó được xem là một trong những cách để thu hút một thế hệ độc giả trẻ đừng quay lưng lại với sách kinh điển. Khi nhìn biểu đồ thống kê sau vào năm 2016 của YouGov, một trang thống kê số liệu thị trường có trụ sở đặt tại Anh, ta có thể hiểu phần nào lý do các nỗ lực của giới xuất bản. Những phiên bản sách đi kèm bìa và minh họa bắt mắt đã có tác dụng phần nào, bởi lẽ rõ ràng đây là thời đại của hình ảnh. Tuy nhiên theo như ý kiến Gower, nhà thiết kế của Penguin thì điều giữ chân người đọc là sự hấp dẫn đến từ bên trong các tác phẩm bởi lẽ không gì làm người đọc thất vọng hơn là việc nhận ra mình đã bị phỉnh phờ bằng những hình vẽ không liên quan đến nội dung sách. Các tác phẩm sở dĩ được gọi là kinh điển bởi lẽ cho dù được viết ra từ thế kỷ XVII hay XVIII thì những vấn đề mà chúng đề cập đến vẫn hoàn toàn phù hợp trong thế kỷ XXI. Và cũng bởi chúng luôn mới mẻ dưới những góc nhìn khác nhau của từng thời đại, các tác phẩm này mang đến niềm cảm hứng vô tận cho cả người đọc và những nghệ sĩ sáng tạo các phiên bản sách khác nhau. "),_c('img',{attrs:{"src":"https://cotich.net/uploads/images/bo-sach-nhung-chuyen-ke-cua-thornton-burgess-2.jpg","thornton":""}}),_c('br'),_c('em',[_vm._v("Bộ sách \"Những chuyện kể của Thornton Burgess\